* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
}
.buy_button {
  border: none;
  outline: none;
  width: 100%;
  padding: 15px 0;
  color: #fff;
  font-size: 16px;
  letter-spacing: 1px;
  background: rgb(99, 99, 32);
  cursor: pointer;
}
.products-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
  width: 100%;
  margin-bottom: 50px;
}
.product-card {
  cursor: pointer;
  transform: scale(1, 1);
  transition: transform 0.5s ease;
  color: #324d67;
}
.product-card:hover {
  transform: scale(1.1, 1.1);
}
.product_img {
  border-radius: 15px;

  transform: scale(1, 1);
  transition: transform 0.5s ease;
  background-color: #324d67;
}

.product-name {
  font-weight: 500;
  text-decoration: none;
  color: black;
}
.product-price {
  font-weight: 800;
  margin-top: 6px;
  color: black;
}

@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Catamaran:wght@300&family=DM+Serif+Display&family=Edu+NSW+ACT+Foundation&family=Exo+2:wght@300&family=Lora:ital@1&family=Merriweather:wght@300&family=Montserrat:wght@200&family=Playfair+Display&family=Rokkitt:wght@100&family=Sawarabi+Mincho&display=swap');
.desc-container {
  display: flex;
  justify-content: center;
  /* border: 10px solid black; */
  width: 80%;
  /* box-shadow: 3px 3px 3px 3px #888888; */
  margin: auto;
  padding-top: 20px;
}

.img-container img {
  border-radius: 10px;
  padding-bottom: 10px;
}

.img-container button {
  border-radius: 10px;

  background-color: red;
}
.content-container {
  padding-left: 40px;
}

.content-container h1 {
  font-family: 'Playfair Display', serif;
  font-weight: 300;
  padding-bottom: 40px;
}

.content-container h3 {
  font-family: 'Playfair Display', serif;
  font-weight: 1000;
  padding-top: 20px;
}

.content-container h2 {
  font-family: 'Playfair Display', serif;
  font-weight: 1000;
  padding-bottom: 20px;
}

.content-container ul li {
  font-family: 'Playfair Display', serif;

  padding-bottom: 10px;
}
/* Footer */

.footer-banner-container {
  padding: 100px 40px;
  border: 10px white solid;
  /* background-color:#d8d7d7 ; */
  /* background-color: rgb(99, 99, 32); */
  border-radius: 25px;
  position: relative;
  /* height: 400px; */
  line-height: 1;
  color: white;
  width: 90%;

  margin: auto;

  background: radial-gradient(
    ellipse at left bottom,
    rgb(17, 17, 17) 0%,
    rgba(173, 90, 90, 0.9) 59%,
    rgba(255, 42, 30, 0.9) 100%
  );
}
.banner-desc {
  display: flex;
  justify-content: space-between;
}
.banner-desc button {
  border-radius: 15px;
  padding: 10px 16px;
  background-color: white;
  color: rgb(99, 99, 32);
  border: none;
  margin-top: 40px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
}
.banner-desc .left h2 {
  font-weight: 900;
  font-size: 80px;
  margin-left: 25px;
}

.banner-desc .left h3 {
  font-weight: 700;
  font-size: 80px;
  margin-left: 25px;
}
.banner-desc .left p {
  margin: 18px;
}
.footer-banner-image {
  position: absolute;

  top: -18%;
  left: 25%;
  /* padding-left: 390px; */
  height: 500px;
  width: 500px;

  margin-top: 50px;
}
.banner-desc .right {
  line-height: 1.4;
}

.banner-desc .right h2 {
  font-weight: 800;
  font-size: 60px;
}

.banner-desc .right h3 {
  font-weight: 500;
  font-size: 60px;
}
.banner-desc .right p {
  font-size: 18px;
}
.banner-desc .right .company-desc {
  font-size: 14px;
  font-weight: 300;
}

.footer-container {
  color: #324d67;
  text-align: center;
  margin-top: 20px;
  padding: 30px 10px;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  justify-content: center;
}
.footer-container .icons {
  font-size: 30px;
  display: flex;
  gap: 10px;
}

/* Hero Banner */

.hero-banner-container {
  margin: auto;
  background: radial-gradient(
    ellipse at left bottom,
    rgb(168, 166, 166) 0%,
    rgba(204, 204, 204, 0.9) 59%,
    rgba(255, 255, 255, 0.9) 100%
  );

  /* background-color: rgb(220, 220, 220); */
  border-radius: 15px;
  position: relative;
  height: 500px;
  line-height: 0.9;
  width: 95%;
}

.hero-banner-container button {
  border-radius: 15px;
  padding: 10px 16px;
  width: 20%;
  background-color: rgb(99, 99, 32);
  color: white;
  border: none;
  margin-top: 50px;
  margin-left: 30px;
  margin-bottom: 30px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  z-index: 10000 !important;
}
@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Catamaran:wght@300&family=Edu+NSW+ACT+Foundation&family=Exo+2:wght@300&family=Lora:ital@1&family=Merriweather:wght@300&family=Sawarabi+Mincho&display=swap');

